.nav {
  display: flex;
  width: 100vw;
  height: 60px;
  position: fixed;
  justify-content: space-between;

  align-items: center;
}
.nav__title {
  margin-left: 10px;
  padding: 10px;
}
.nav__links {
  display: flex;
  margin-right: 20px;
  align-items: center;
}
.nav__links h3 {
  padding: 10px;
}
@media (max-width: 500px) {
  .nav__title {
    margin-left: 10px;
    padding: 10px;
    font-size: 1em;
  }
  .nav__links h3 {
    padding: 10px;
    font-size: 0.7em;
  }
}
